import React, { Component } from 'react';

class Input extends Component {

    render() {
        return (
            <div>
                <input
                    id="inp"
                    value={this.props.inpValue}
                    type="text"
                    onChange={this.props.handleChange}
                />
                <button onClick={this.props.addTodo}>添加</button>
            </div>
        );
    }
}

export default Input;